.header-cart__link {
  position: relative;
  padding: 13px 11px 13px clamp(2.75rem, 2.556rem + 0.86vw, 3.25rem);
  font-weight: 700;
  font-size: clamp(0.688rem, 0.567rem + 0.54vw, 1rem);
  color: var(--white);
  border-radius: clamp(0.688rem, 0.567rem + 0.54vw, 1rem);
  height: 48px;
  background-color: var(--red);
  background-image: url("../images/icons/icon-cart.svg");
  background-position: left 11px center;
  background-repeat: no-repeat;
  background-size: clamp(1.375rem, 1.278rem + 0.43vw, 1.625rem) clamp(1.188rem, 1.115rem + 0.32vw, 1.375rem);
}
.header-cart__link.active {
  cursor: pointer;
}
.header-cart__link.active .header-cart__title {
  display: none;
}
.header-cart__link.active .header-cart__quantity {
  display: inline-flex;
}
.header-cart__link.active .header-cart__summ {
  display: block;
}
.header-cart__quantity {
  display: inline-flex;
  display: none;
  position: absolute;
  top: 3px;
  left: 28px;
  justify-content: center;
  align-items: center;
  width: clamp(0.938rem, 0.817rem + 0.54vw, 1.25rem);
  height: clamp(0.938rem, 0.817rem + 0.54vw, 1.25rem);
  font-size: clamp(0.5rem, 0.403rem + 0.43vw, 0.75rem);
  color: var(--red);
  background-color: var(--yellow);
  border-radius: 100%;
}
.header-cart__summ {
  display: none;
}